import React, { Component } from 'react';
import './slider.css';
import z2 from '../../../static/subpages/page1388/2.jpg';
import z3 from '../../../static/subpages/page1388/3.jpg';
import z4 from '../../../static/subpages/page1388/4.jpg';
import z5 from '../../../static/subpages/page1388/5.jpg';
import z6 from '../../../static/subpages/page1388/6.jpg';
import z9 from '../../../static/subpages/page1388/9.jpg';

export default class Slider extends Component {
   constructor(props) {
      super(props);
      this.images = [z2, z3, z4, z5, z6, z9];
   }

   imgDiv(text, img) {
      return(
         <div className="slide">
            <img src={img} className="slider-img"></img>
            <div className="slider-text">{text}</div>
         </div>
      );
   }

   render() {
      return (
         <div id="slider">
            {this.images.map(image => <img src={image}></img>)}
         </div>
      )
   }
}
